.protected__layout__container {
  display: flex;
  height: 100vh;
  width: 100vw;

  .protected__layout__header {
    position: sticky;
    top: 0;
    z-index: 1;
  }
  .protected__layout__content__space {
    // display: flex;
    // background-color: #0b1b6e;
    .protected__layout__sidebar__container {
      overflow-x: scroll;
      min-width: max-content;
      background-color: #0b1b6e;
    }

    .page__container {
      background-color: white;
      width: 100%;
      padding: 34px 41px 48px 25px;
    }
    .main__content {
      // background-color: $default-background-grey;
      // padding: 23px 34px 27px 27px;
      padding: 10px 60px;
      height: 100vh;
      width: 80vw;
      overflow-y: scroll;
      overflow-x: scroll;
      // overflow-y: auto;
      // max-height: 100vh;
      // width: 100%;
      // height: 100vh;
    }
    @media only screen and (max-width: 800px) {
      .main__content {
        width: 100vw;
      }
    }
  }
}
