.add__replenish__table {
    .breadCrums {
      padding: 0;
      border: none;
      font-weight: 400;
      color: #000;
      font-size: 16px;
    }
    .multipleicon_container{
      padding: 1.5rem;
    }
    .p-breadcrumb .p-breadcrumb-list li .p-menuitem-link .p-menuitem-text {
      // color: #4b5563;
      color: #000;
      font-size: 16px;
      font-weight: 400;
      font-family: 'Inter', sans-serif;
    }
    .pettycash__title {
      color: #111927;
      font-size: 30px;
      font-style: normal;
      font-weight: 600;
      font-family: 'Inter', sans-serif;
      display: flex;
      align-items: center;
      gap: 5px;
      cursor: pointer;
    }
    .add__btn {
        color: #fff;
        font-size: 18px;
        font-weight: 500 !important;
        background-color: #6366f1;
        border-radius: 60px !important;
        padding-right: 1rem;
        padding-left: 1rem;
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        margin: 10px;
        font-family: 'Inter', sans-serif;
        column-gap: 0.4rem;
        padding-top: 0.6rem;
        padding-bottom: 0.6rem;
    }
    .btn__container {
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
    .sub__request__title {
      color: #111927;
      font-family: 'Inter', sans-serif;
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
    }
