.claim__details__container {
  .p-card {
    background: #ffffff;
    color: #111927;
    box-shadow: none !important;
    border-radius: 20px !important;
    background-color: #fff !important;
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.08)) !important;
    .p-card-body {
      padding: 24px;
    }
  }
  .claim__details__container__titles {
    font-size: 34px;
    font-weight: 600;
    font-family: Poppins;
    line-height: 51px;
    color: #111927;
  }
  .next_but_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin-top: 10px;
  }

  .claim__details__container__back__btn {
    display: flex;
    align-items: center;
    gap: 10px;

    .claim__details__container__back__btn__title {
      font-size: 16px;
      font-weight: 400;
      font-family: Poppins;
      line-height: 24px;
      color: #111927;
    }
  }
  .claim__details__card__container__title {
    font-size: 34px;
    font-weight: 600;
    font-family: Poppins;
    line-height: 51px;
    color: #111927 !important;
  }
  .check__box__container {
    display: flex;
    align-items: center;
    gap: 15px;
    .check__box__container__title {
      font-size: 16px;
      font-weight: 400;
      font-family: Poppins;
      line-height: 24px;
      color: #111927 !important;
    }
  }
  .claim__details__card__sub__title {
    font-size: 24px;
    font-weight: 400;
    font-family: Poppins;
    line-height: 36px;
    color: #111927 !important;
  }
  .claim__write__field {
    width: 100%;
    height: 266px;
    // padding-top: 30px;

    font-size: 16px;
    font-weight: 500;
    font-family: Poppins;
    line-height: 24px;
    color: #111927;
    border-radius: 10px;
  }
}
