.request__form {
  .checkbox__container {
    justify-content: end;
    gap: 16px;
    align-items: center;
  }
  .breadCrums {
    padding: 0;
    border: none;
    font-weight: 500;
    color: #000;
    font-size: 16px;
  }

  .p-dropdown {
    background: #ffffff;
    border: 1px solid #d1d5db;
    transition: background-color 0.2s, color 0.2s, border-color 0.2s,
      box-shadow 0.2s;
    border-radius: 6px;
    margin-top: 0.5rem !important;
    width: 100% !important;
  }
  .input__block .p-inputtext {
    font-family: var(--font-family);
    font-feature-settings: var(--font-feature-settings, normal);
    font-size: 1rem;
    color: #4b5563;
    background: #ffffff;
    padding: 0.75rem 0.75rem;
    border: 1px solid #d1d5db;
    transition: background-color 0.2s, color 0.2s, border-color 0.2s,
      box-shadow 0.2s;
    appearance: none;
    border-radius: 6px;
    margin-top: 0.5rem !important;
    width: 100%;
  }
  .p-breadcrumb .p-breadcrumb-list li .p-menuitem-link .p-menuitem-text {
    // color: #4b5563;
    color: #000;
    font-size: 16px;
    font-weight: 500;
    font-family: "Inter", sans-serif;
  }
  .pettycash__title {
    color: #111927;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
    font-family: "Inter", sans-serif;
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
  }
  .add__btn {
    color: #fff;
    font-size: 18px;
    font-weight: 500 !important;
    background-color: #6366f1;
    border-radius: 60px !important;
    padding-right: 1rem;
    padding-left: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin: 10px;
    font-family: "Inter", sans-serif;
    column-gap: 0.4rem;
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;
  }
  .btn__container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .p-card .p-card-content {
    padding: 0rem 0 !important;
  }
  .check__box__container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    gap: 10px;
  }
  .check__box__text {
    color: #111927;
    font-family: "Inter", sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  .calendar_container {
    width: 100% !important;
    margin-top: 8px;
  }
  .labelfield_container {
    color: #111927;
    font-family: "Inter", sans-serif;
    font-size: 16px;
    font-weight: 500;
  }
  .p-calendar-w-btn-right .p-datepicker-trigger {
    background-color: #fff !important;
    border-left: none !important;
    border: 1px solid #d1d5db;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    color: #6366f1;
  }
  .btn__container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .sub__request__title {
    color: #111927;
    font-family: "Inter", sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }
  //   .sub__container{
  //     padding-left: 5%;
  //     padding-right: 5%;
  //   }
  .sub__container__title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    // padding-left: 4%;
  }
}
