
.petty__add__container {
  .label_header {

    color: #111927;
    font-family: 'Inter', sans-serif;
    font-size: 30px;
    // font-style: normal;
    font-weight: 600;
    margin-left: 10px;
    // line-height: normal;
}
  .add__sub__title {
      color: #111927;
      font-size: 30px;
      font-weight: 600;
      font-family: 'Inter', sans-serif;
      display: flex;
      align-items: center;
  }

  .breadCrums__view__add__screen {
      border: none;
      padding: 0;
      font-family: 'Inter', sans-serif;
      color: #111927;
      font-size: 14px;
      font-weight: 600;

  }
  .p-breadcrumb .p-breadcrumb-list li:last-child .p-menuitem-text {
      color: #B1B1B1;
  }
  .p-breadcrumb .p-breadcrumb-list li .p-menuitem-link .p-menuitem-text {
    // color: #4b5563;
    color: #000;
    font-size: 16px;
    font-weight: 500;
    font-family: 'Inter', sans-serif;
}

  .add__account__sub__container {
      box-shadow: 0px 0px 20px 0px #00000014;
      border-radius: 10px;

      .dropdown__add__sub {
          width: 100%;
          margin: 6px 0;
          border-radius: 10px;
      }

      .label__sub__add {
          font-family: 'Inter', sans-serif;
          font-size: 16px;
          font-weight: 500;
          line-height: 19px;
          letter-spacing: 0em;
          text-align: left;

      }
      .calender_container_claim {
        position: relative;
        width: 100% !important;
        .calender_icon_claim {
          position: absolute;
          right: 16px;
          bottom: 8px;
          z-index: 1;
        }
    }
    .calender_field_claim {
         
        width: 100% !important;
        border-radius: 10px;
        .p-inputtext {
          margin: 6px 0 0 0;
          border-radius: 5px;
          height: 3.125rem;
        //   border-style: none;
        }
          .p-inputtext::placeholder{
            color:'red'
          }
        button {
          position: absolute;
          background-color: transparent;
          right: 6px;
          border: 0;
          top: 18px;
          z-index: 2;
          svg.p-icon {
            display: none;
          }
        }
      }

  }
  .btn__view__Add{
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      .save__add__btn{
          height: 40px;
          width: 130px;
          background-color: #6366F1
          ;
          border-radius: 30px;
          font-size: 14px;
          font-family: 500;
          color: #fff;
      }
  }
  .custom-modal-overlay {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1rem;
      z-index: 1;
  }

  .custom-modal {
      background-color: #fff;
      overflow-y: auto;
      position: relative;
      width: 22%;
      border-radius: 20px;
      height: 200px;
      display: grid;
      justify-content: center;
      align-items: center;
      padding: 10px;
  }


  .popup__icon {
      display: flex;
      justify-content: center;
      align-items: center;
  }

  .popup__text {
      color: #111927;
      text-align: center;
      font-size: 17px;
      font-weight: 500;
      font-family: 'Inter', sans-serif;
  }
  .popup__token__text{
      color: #29CE00;
  }

  .modal-header {
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      font-size: 18px;
      font-weight: bold;
      margin-bottom: 20px;
      font-family: 'Inter', sans-serif;
  }

  .modal-body {
      color: #fff;
  }
  .card__container {
    background: #fff;
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.08));
    padding: 10px 0;
    border-radius: 10px;
    margin: 30px 0;
  }

  .input__field__reversal {
    color: #111927;
    width: 100%;
    border-radius: 10px;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    .p-dropdown-label {
      color: #111927;
      font-family: 'Inter', sans-serif;
      font-size: 14px;
      font-weight: 400;
    }
  }
  .input__field__reversal__inactive {
    color: #000;
    width: 100%;
    border-radius: 10px;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    .p-component:disabled {
      opacity: 1 !important;
    }
  }

  .input__label__reversal {
    color: #111927 !important;
    font-size: 16px !important;
    font-style: normal !important;
    font-weight: 500 !important;
    line-height: normal !important;
    font-family: 'Inter', sans-serif;
  }
  .input__label__reversal__inactive {
    color: #b1b1b1 !important;
    font-size: 16px !important;
    font-style: normal !important;
    font-weight: 500 !important;
    line-height: normal !important;
    font-family: 'Inter', sans-serif;
  }
 
}
  