.add__receipts__container {
  .label_header {

    color: #111927;
    font-family: 'Inter', sans-serif;
    font-size: 30px;
    // font-style: normal;
    font-weight: 600;
    margin-left: 10px;
    // line-height: normal;
}
  .breadCrums {
    padding: 4px 0;
    border: none;
    font-weight: 500;
    color: #000;
    font-size: 16px;
  }
  .p-breadcrumb .p-breadcrumb-list li .p-menuitem-link .p-menuitem-text {
    // color: #4b5563;

    color: #000;
    font-size: 16px;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
  }
  .pettycash__title {
    color: #111927;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
  }
  .add__btn {
    color: #fff;
    font-size: 18px;
    font-weight: 500 !important;
    background-color: #6366f1;
    border-radius: 60px !important;
    margin: 10px;
    font-family: 'Inter', sans-serif;
    column-gap: 0.4rem;
    padding: 14px 64px;
  }
  .btn__container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .sub__request__title {
    color: #111927;
    font-family: 'Inter', sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }
  .sub__container__title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 4%;
  }
  .p-card .p-card-content {
    padding: 0rem 0;
  }
  .input__block .p-inputtext {
    font-family: var(--font-family);
    font-feature-settings: var(--font-feature-settings, normal);
    font-size: 1rem;
    color: #4b5563;
    background: #ffffff;
    padding: 0.75rem 0.75rem;
    border: 1px solid #d1d5db;
    transition: background-color 0.2s, color 0.2s, border-color 0.2s,
      box-shadow 0.2s;
    appearance: none;
    border-radius: 6px;
    margin-top: 0.5rem !important;
    width: 100% !important;
  }
  .p-dropdown {
    background: #ffffff;
    border: 1px solid #d1d5db;
    transition: background-color 0.2s, color 0.2s, border-color 0.2s,
      box-shadow 0.2s;
    border-radius: 6px;
    margin-top: 0.5rem !important;
    width: 100% !important;
  }
}
