.leadlisting__overal__container {
    padding: 20px;


    .corrections__table__main {
        margin: 2rem;

        thead {
            tr {
                th {
                    border: 0 !important;
                    padding: 0 !important;
                }

                .p-column-header-content {
                    .p-column-title {
                        color: #111927;
                        font-family: 'Inter', sans-serif;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 500;
                        line-height: normal;
                        padding: 1rem 1rem;
                        width: 100%;
                    }
                }
            }
        }

        tbody {
            tr {
                td {
                    color: #111927;
                    font-family: 'Inter', sans-serif;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: normal;
                    padding: 1rem 1rem !important;
                }
            }
        }

        .p-paginator-first,
        .p-paginator .p-paginator-prev,
        .p-paginator .p-paginator-next,
        .p-paginator .p-paginator-last {
            background-color: transparent;
            border: 0 none;
            color: #1c2536;
            min-width: 0.5rem;
            height: 3rem;
            margin: 0.143rem;
            transition: box-shadow 0.2s;
            border-radius: 50%;
        }

        .p-paginator-bottom {
            border-width: 0;
            justify-content: flex-end;

            span {
                color: #1c2536 !important;
                font-family: 'Inter', sans-serif;
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
            }

            .table__selector {
                text-align: center;
                flex: 1 0;

                .pagedropdown_container {
                    border: none;
                    margin: 0 !important;
                    background-color: #FAFAFA;
                }
            }
        }

