    .p-paginator .p-paginator-first,
    .p-paginator .p-paginator-prev,
    .p-paginator .p-paginator-next,
    .p-paginator .p-paginator-last {
        background-color: transparent;
        border: 0 none;
        color: #1C2536;
        min-width: 0.5rem;
        height: 3rem;
        margin: 0.143rem;
        transition: box-shadow 0.2s;
        border-radius: 50%;
    }
    .pagedropdown_container {
        // margin-right: 20%;
        border-style: hidden;
        background-color: #FAFAFA;
        padding: 0;
        margin: 0;
    }

    .row__count__view {
        display: flex;
        justify-Content: center;
        align-Items: center;
        width: 70%
    }

    paginator-bottom {
        border-width: 0 0 1px 0;
        border-radius: 0;
        border-color: transparent;
    }

    .add__journal__vocture {
        box-shadow: rgba(241, 244, 247, 0.3) 0px 1px 2px 0px, rgba(169, 174, 179, 0.15) 0px 2px 6px 2px;
        border-radius: 10px;

        .dropdown__add__sub {
            width: 100%;
            margin: 6px 0;
            border-radius: 10px;
        }

        .label__sub__add {
            font-family: 'Inter', sans-serif;
            font-size: 16px !important;
            font-weight: 500 !important;
            text-align: left;

        }

    }
    .add__journal__vocture__view {
        // box-shadow: rgba(241, 244, 247, 0.3) 0px 1px 2px 0px, rgba(169, 174, 179, 0.15) 0px 2px 6px 2px;
        // border-radius: 10px;

        .dropdown__add__sub {
            width: 100%;
            margin: 6px 0;
            border-radius: 10px;
        }

        .label__sub__add {
            font-family: 'Inter', sans-serif;
            font-size: 16px !important;
            font-weight: 500 !important;
            text-align: left;

        }

    }

    .btn__view__Add {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;

        .save__add__btn {
            height: 40px;
            width: 130px;
            background-color: #6366F1;
            border-radius: 30px;
            font-size: 14px;
            font-family: 500;
            color: #fff;
        }
    }

    .custom-modal-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 1rem;
        z-index: 1;
    }

    .custom-modal {
        background-color: #fff;
        overflow-y: auto;
        position: relative;
        width: 22%;
        border-radius: 20px;
        height: 200px;
        display: grid;
        justify-content: center;
        align-items: center;
        padding: 10px;
    }

    .calender_container_claim {
        position: relative;
        width: 100% !important;

        .calender_icon_claim {
            position: absolute;
            right: 16px;
            bottom: 8px;
            z-index: 1;
        }
    }

    .calender_field_claim {

        width: 100% !important;
        border-radius: 10px;
        padding: 0;
        margin: 0 !important;

        .p-inputtext {
            margin: 6px 0 0 0;
            border-radius: 10px;
            height: 3.125rem;
            //   border-style: none;
        }

        .p-inputtext::placeholder {
            color: 'red'
        }

        button {
            position: absolute;
            background-color: transparent;
            right: 6px;
            border: 0;
            top: 18px;
            z-index: 2;

            svg.p-icon {
                display: none;
            }
        }
    }


    .popup__icon {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .popup__text {
        color: #111927;
        text-align: center;
        font-size: 17px;
        font-weight: 500;
        font-family: 'Inter', sans-serif;
    }

    .popup__token__text {
        color: #29CE00;
    }

    .formik__errror {
        font-size: 10px !important;
        color: red !important;
    }

    .modal-header {
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 20px;
        font-family: 'Inter', sans-serif;
    }

    .modal-body {
        color: #fff;
    }


}