.add__disbursement__table {
  .table__top__btn__container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .breadCrums {
    padding: 0;
    border: none;
    font-weight: 500;
    color: #000;
    font-size: 16px;
  }
  .pagedropdownunique_container{
    background-color: #FAFAFA;
    border:none
  }
  .p-breadcrumb .p-breadcrumb-list li .p-menuitem-link .p-menuitem-text {
    // color: #4b5563;
    color: #000;
    font-size: 16px;
    font-weight: 500;
    font-family: "Inter", sans-serif;
  }
  .pettycash__title {
    color: #111927;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
    font-family: "Inter", sans-serif;
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
  }
  .add__btn {
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    background-color: #6366f1;
    border-radius: 60px !important;
    height: 45px;
    width: 120px;
    margin: 10px;
    font-family: "Inter", sans-serif;
  }
  .btn__container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .sub__request__title {
    color: #111927;
    font-family: "Inter", sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }
  //   .sub__container{
  //     padding-left: 5%;
  //     padding-right: 5%;
  //   }
  .sub__container__title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  
  //   .table__container{
  //     padding-left: 5%;
  //     padding-right: 5%;
  //   }
  .empty-table-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 2%;
    padding-bottom: 2%;
  }
  .p-card .p-card-content {
    padding: 0rem 0;
  }
  .delete__btn {
    color: #ffffff;
    background: transparent !important;
    border: 0px solid #fff !important;
    padding: 0.25rem 0.75rem !important;
    font-size: 1rem !important;
    transition: background-color 0.2s, color 0.2s, border-color 0.2s,
      box-shadow 0.2s;
    border-radius: 10px !important;
  }
  .input__block .p-inputtext {
    font-family: var(--font-family);
    font-feature-settings: var(--font-feature-settings, normal);
    font-size: 1rem;
    color: #4b5563;
    background: #ffffff;
    padding: 0.75rem 0.75rem;
    border: 1px solid #d1d5db;
    transition: background-color 0.2s, color 0.2s, border-color 0.2s,
      box-shadow 0.2s;
    appearance: none;
    border-radius: 6px;
    margin-top: 0.5rem !important;
    width: 100% !important;
  }
  .p-datatable .p-datatable-tbody > tr > td {
    text-align: left;
    border: 1px solid #e5e7eb;
    border-width: 0 0 1px 0;
    padding: 1rem 0.5rem;
  }
}
